@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,600,700|PT+Sans:400,500,700|Rozha+One);
header{position:absolute;color:#fff;width:100%;padding:20px;z-index:20}header .header-nav{display:flex;justify-content:space-between}header .logo{display:inline-block;padding:0 10px}header .nav{display:inline-block;text-align:right}header a{display:inline-block;color:#ffffff;text-decoration:none;padding:0 10px}header .fa-bars{display:none;font-size:2rem}header #hamburger{display:none}#TopImg{position:relative;background:black;min-height:500px;height:100vh;background:linear-gradient(135deg, rgba(0,0,0,0.4) 0, #002d77 100%);background-image:linear-gradient(135deg, rgba(0,0,0,0.4) 0, #000 100%),url(/react-ts-sass-restaurant-app/static/media/topimg.2011bc10.jpg);background-position:center;background-size:cover}#TopImg .title{padding:150px 0 80px;text-align:right}#TopImg .title h1{font-family:"Rozha One",serif;text-transform:uppercase;color:#fff;font-weight:300;font-size:6rem}#TopImg .title h5{text-transform:uppercase;color:#fff;font-weight:300}#TopImg .contact-info{width:100%;position:absolute;bottom:50px;right:0;text-align:right;color:#adadad;font-size:0.8rem;line-height:1.5rem}#TopImg .contact-info strong{font-weight:bold;color:#fff}#TopImg .contact-info .booking{margin-bottom:10px;color:#adadad}#TopImg .contact-info h2{font-size:2rem;margin-bottom:20px;color:#fff}@media only screen and (max-width: 800px){#TopImg .title h1{font-size:3rem}}#OurStory{padding:100px 0}#OurStory .quote{color:#adadad;margin:0.5rem 0 3rem}#OurStory .quote strong{color:#202020;font-weight:700}#OurStory .reserve-btn{font-family:"Rozha One",serif;display:inline-block;border:1px solid #202020;padding:20px 40px;text-align:center;text-decoration:none;color:#202020;text-transform:uppercase;transition:all 0.5s ease-in-out}#OurStory .reserve-btn:hover{background:#911a20;color:#fff}#OurStory .video-img{max-width:400px;height:550px;background:linear-gradient(135deg, rgba(0,0,0,0.1) 0, #002d77 100%);background-image:linear-gradient(135deg, rgba(0,0,0,0.1) 0, #000 100%),url(/react-ts-sass-restaurant-app/static/media/cookingart.f6b77b8f.jpg);background-position:center;background-size:cover;margin-top:40px}#SpecialMenu{padding:100px 0}#SpecialMenu h2,#SpecialMenu h5{text-align:center}#SpecialMenu .box{background:#fff;padding:20px}#SpecialMenu .box .box-img{width:100%;height:200px;background:linear-gradient(135deg, rgba(0,0,0,0.4) 0, #002d77 100%);background-image:linear-gradient(135deg, rgba(0,0,0,0.4) 0, #000 100%),url(/react-ts-sass-restaurant-app/static/media/salad.ff3fea3d.jpg);background-position:center;background-size:cover;position:relative;margin-bottom:75px}#SpecialMenu .box .price-circle{position:absolute;bottom:0;margin:0 auto;font-family:"Rozha One",serif;font-size:2.7rem;background:#fff;height:100px;width:100px;border-radius:50%;display:flex;align-items:center;justify-content:center;left:50%;transform:translate(-50%, 50%);box-shadow:0 0 0 10px rgba(255,255,255,0.41)}#SpecialMenu .box .title{display:block;font-weight:600;font-size:0.9rem;text-align:center}#SpecialMenu .box .details{font-weight:300;font-size:0.8rem;text-align:center;line-height:2;margin:0}#SpecialMenu .boxes{padding-bottom:50px}#SpecialMenu a.link{color:#000;font-family:"Rozha One",serif;text-align:center;display:block}#RandomQuote{padding:180px 0;background:linear-gradient(135deg, rgba(0,0,0,0.2) 0, #002d77 100%);background-image:linear-gradient(135deg, rgba(0,0,0,0.2) 0, #000 100%),url(/react-ts-sass-restaurant-app/static/media/cooking.9d83f56b.png);background-position:right;background-size:cover}#RandomQuote h1{color:#fff;font-family:"Rozha One",serif;font-size:2.7rem;text-transform:capitalize;text-align:center;max-width:600px;margin:0 auto 50px;line-height:1.3;font-weight:500}#RandomQuote span{color:#fff;font-size:1rem;font-weight:300;text-transform:capitalize;text-align:center;display:block}#Reviews{text-align:right}#Reviews img{max-width:600px}#Reviews h4{font-weight:600;font-size:0.9rem;margin-bottom:2rem}#Reviews .author{font-size:0.8rem;color:#adadad;font-weight:300;text-transform:capitalize;margin-bottom:2rem}#Reviews .author strong{font-weight:700;color:#202020}#Reviews .arrows .fa-arrow-left,#Reviews .arrows .fa-arrow-right{font-size:1.5rem;padding:20px 10px;color:#adadad;cursor:pointer}#Reviews .arrows .fa-arrow-left.ready,#Reviews .arrows .fa-arrow-right.ready{color:#202020}@media only screen and (max-width: 800px){#Reviews img{max-width:250px}}#ContactUs{padding:100px 0}#ContactUs h2,#ContactUs h5{text-align:center}#ContactUs .box{padding:70px;background:#fff}#ContactUs .box .title{font-family:"Rozha One",serif;font-size:2rem;margin-bottom:2rem}#ContactUs h6{font-size:0.9rem;font-weight:600;color:#aaa;line-height:1.7;color:#202020}#ContactUs p{font-weight:400;margin-bottom:20px}#ContactUs p strong{color:#202020;text-transform:capitalize;font-weight:600}#ContactUs p a{color:#adadad;text-decoration:none;font-weight:600}#ContactUs p a:hover{color:#911a20}footer{padding:100px 0;background:#131a22;text-align:center}footer nav{margin:0 auto 50px}footer nav a{color:#fff;display:inline-block;padding:10px 20px 10px 0;text-decoration:none}footer .social-media{margin:0 0 50px;padding:0}footer .social-media li{display:inline-block;padding:20px}footer .social-media li a{color:#fff}footer .social-media li a:hover{color:#911a20}footer .copyright{color:#aaa}body{color:#202020}h2{font-family:"Rozha One",serif;font-size:3rem;margin:0.5rem 0 3rem;text-transform:capitalize;color:#202020}h5{color:#aaa;font-size:0.7rem;margin:0.5rem 0 1rem;text-transform:uppercase;letter-spacing:5px}p{font-size:0.8rem;color:#adadad;line-height:2;margin:0.5rem 0 3rem;font-weight:300}a{transition:all 0.5s ease-in-out}a:hover{color:#911a20}strong{font-weight:600}.texturebg{background:#f7f7f7;background-image:url(/react-ts-sass-restaurant-app/static/media/texture.47b6aae1.jpg);background-position:center;background-repeat:repeat}*{box-sizing:border-box;margin:0;padding:0}body{font-family:"Open Sans Condensed",sans-serif,sans-serif;width:100%;height:100vh;overflow-x:hidden;background:#fff}

